	}
	.rowheight {
		height: 100px !important;
	}
	.paddingleft >>> .v-data-table-header th {
		padding-left: 20px !important;
		padding-right: 20px !important;
	}
	.paddingleft >>> tr :nth-child(3){
		padding-left: 60px;
	}
	.radiostyle {
		font-size:20px;
		padding-left:20px;
		padding-right:20px;
		font-weight: bold;
	}
    #loading_img{
        position: relative;
        top:5%;
        left:43%;
    }
</style>
<style>
	input[type="date"]::-webkit-calendar-picker-indicator {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: transparent;
		color: transparent;
		z-index: 1;
	}
</style>
<template>
	<v-layout v-if="this.$session.get('user_name') != null" style="width:100%;">
		<audio id="msg_audio" src="../../assets/msg_bell.wav" type="audio/wav"></audio>
		<div style="margin-bottom:20px; width:100%;">
			<div style="margin-top:30px; margin-left:5px; font-size:30px; font-weight:bold;" class="fontDefault">충북사슴 MES 시스템
				<div @click="LogOut()" style="float:right;">{{ user_name }} </div>
				<div style="float:left;">&nbsp;&nbsp;&nbsp;</div>
				&nbsp;&nbsp;&nbsp;
			</div>
			<div style="margin-top:5px; margin-left:28px; font-size:20px; font-weight:bold;" class="fontDefault">{{ nowTime }}</div>
			<div style="height:10px;"></div>
			<div style="margin-left:25px; font-size:20px;width:100%;">
				<v-row style="width:100%;">
					<v-col xl="12" md="12" sm="12" cols="12" align-self="center" style="width:100%; padding-bottom:0px;margin-bottom:0px;cursor:pointer;">
						<v-data-table no-data-text="조회된 작업이 없습니다." style="font-weight:bold; font-size:20px;width:100%;" mobile-breakpoint="0" class="elevation-1" v-model="selected_items" @click:row="onClickRow" @input="onChangeCheck($event)" item-key="lotno" :headers="headers" :items="document" :loading="loading" :footer-props="{ 'items-per-page-options': [8, -1] }" :items-per-page="8">
							<template slot="item.clm_machine_name" slot-scope="props">
								<v-btn class="mx-2" icon>
									<v-btn width="100%" color="red" style="color:white; font-weight:bold;" v-on:click="OpenPackage(props.item)">생산완료</v-btn>
								</v-btn>
							</template>
						</v-data-table>
					</v-col>  
				</v-row>
				<v-row justify="center">
					<v-dialog v-model="PackageModal" persistent max-width="450" @keydown.esc="PackageModal = false">
						<v-card>
						<v-toolbar class="fontDefault" color="light-blue" dark>{{ title_joborder_id }} 생산</v-toolbar>
						<v-card-text style="padding-top:15px;">
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">상대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_high_yn =='N'">
										<v-text-field label="수량"  v-model="clm_high_count" type="number" />
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_high_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">중상대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_middle_high_yn =='N'">
										<v-text-field label="수량"  v-model="clm_middle_high_count"  type="number"/>
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_middle_high_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">중대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_middle_yn =='N'">
										<v-text-field label="수량"  v-model="clm_middle_count" type="number"/>
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_middle_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">하대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_low_yn =='N'">
										<v-text-field label="수량"  v-model="clm_low_count" type="number" />
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_low_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">분골</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_bones_yn =='N'">
										<v-text-field label="수량"  v-model="clm_bones_count" type="number" />
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_bones_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">분골상대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_bones_high_yn =='N'">
										<v-text-field label="수량"  v-model="clm_bones_high_count"  type="number"/>
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_bones_high_count" disabled/>
									</template>
								</v-col>
							</v-row>
							<v-row>
								<v-col xl="4" md="4" sm="4" cols="4" style="padding-bottom:0px;margin-bottom:0px;padding-top:30px;margin-top:0px;">
									<span style="font-weight:bold;font-size:25px;">상대분대</span>
								</v-col>
								<v-col xl="8" md="8" sm="8" cols="8" style="padding-bottom:0px;margin-bottom:0px;padding-top:10px;margin-top:0px;">
									<template v-if="this.clm_high_bun_yn =='N'">
										<v-text-field label="수량"  v-model="clm_high_bun_count"  type="number"/>
									</template>
									<template v-else>
										<v-text-field label="수량"  v-model="clm_high_bun_count" disabled/>
									</template>
								</v-col>
							</v-row>
						</v-card-text>
						<v-card-actions class="justify-end">
							<template v-if="this.$router.app.$route.query.update_yn=='Y'">
								<v-btn text @click="SavePackageCount">생산완료</v-btn>
							</template>
							<v-btn text @click="PackageModal = false">닫기</v-btn>
						</v-card-actions>
						</v-card>
					</v-dialog>
				</v-row>
			</div>
		</div>
    </v-layout>
	<v-layout v-else>
		<v-row justify="center">
			<v-dialog v-model="loginModal" persistent max-width="300">
				<v-card>
					<v-card-title>
					충북사슴 MES 시스템
					</v-card-title>
					<v-card-text style="padding-bottom:0px;">
						<v-text-field label="ID" v-model="username" v-on:keyup.enter="LoginConfirm" />
						<v-text-field label="Password" type="password" v-model="password" v-on:keyup.enter="LoginConfirm" />
					</v-card-text>
					<v-card-actions style="padding-top:0px;">
					<v-spacer></v-spacer>
					<v-btn color="black darken-1" text v-on:click="LoginConfirm" style="font-size:20px;">
						로그인
					</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
		</v-row>
	</v-layout>
</template>
<script>
import axios from 'axios';

export default {
	data() {
		return {
			user_agent: String(navigator.userAgent).toLowerCase(),
			user_id: this.$session.get('user_id'),
			user_name: this.$session.get('user_name'),
			user_ip_a: '',
			user_ip_b: '',
			username:'',
			password:'1234',
			MessageModal: false,
			loginModal: false,
			bell_gubun: '',
			bell_items: [],
			bell_index: '0',
			msg_id: '',
			msg_sender_name: '',
			msg_sender_datetime: '',
			msg_title: '',
			msg_text: '',
			keyword: '',
			save_yn:'N',
			search_date: '',
			search_gubun: '',
			plan_gubun_items: [],
			/* Grid 변수 */
			loading: false,
			headers: [
				{ text: '작업지시서 번호', align: 'center', value: 'clm_joborder_id', width: 120 },
				{ text: '작업예정일자', align: 'start', value: 'clm_planned_datetime', width: 120 },
				{ text: '작업시작일시', align: 'center', value: 'clm_start_datetime', width: 120 },
				{ text: '작업종료일시', align: 'center', value: 'clm_end_datetime', width: 120 },
				{ text: '생산', align: 'center', value: 'clm_machine_name' , width: 150 }
			],
			document: [],
			options: {
				multiSort: false,
				sortBy: [],
				sortDesc: [],
				page: 1,
				itemsPerPage: 5,
			},
			selected_items: [],
			//중복방지
			loading_yn:'N',
			document2: [],
			// -- 임시 콘솔오류 방지용 --
			nowTime: '',
			start_date: '',
			start_time: '',
			finish_date: '',
			finish_time: '',
			/* 충북사슴 */
			clm_high_count : '',
			clm_middle_high_count : '',
			clm_middle_count : '',
			clm_low_count : '',
			clm_bones_count: '',
			clm_bones_high_count: '',
			clm_high_bun_count: '',
			title_joborder_id:'',
			//disabled 구분
			clm_high_yn : 'N',
			clm_middle_high_yn : 'N',
			clm_middle_yn : 'N',
			clm_low_yn : 'N',
			clm_bones_yn : 'N',
			clm_bones_high_yn : 'N',
			clm_high_bun_yn : 'N',
			PackageModal : false,
		}
	},
	beforeCreate: function () {
	    
	},
	created() {
		this.setDate();
	},
	mounted() {
		if (!this.$session.exists()) {
			this.loginModal = true;
		}else{
			this.user_agent = String(navigator.userAgent).toLowerCase();
		}
		var link = this.$router.app.$route.query.from_location;
		if(String(this.user_agent).indexOf('samsung') > -1 || String(this.user_agent).indexOf('sm-p610') > -1 || String(this.user_agent).indexOf('android') > -1){
			(()=>{
				window.__WEBVIEW_BRIDGE__={
					init:function(){
						document.addEventListener('message', function(e){
							ReactNativeWebView.postMessage("MOB001");
						});
					}
				};
				window.__WEBVIEW_BRIDGE__.init();
			})();
		}

		axios.get('/back/Common/CodeList',{ // 주간/야간 구분
			params: { code_id: '0007' }
		}).then(
			response => {
				this.plan_gubun_items = response.data;
			}
		);

		this.search_date = this.setDate().year + "-" + this.setDate().month + "-" + this.setDate().date;
		if(Number(this.setDate().hh) >= 19){
			this.search_gubun = '0002';
		}else{
			this.search_gubun = '0001';
		}

		this.fnc_bell();
		setInterval(this.fnc_bell.bind(this), 10000);

		this.getList();

		if(String(this.user_agent).indexOf('samsung') > -1 || String(this.user_agent).indexOf('sm-p610') > -1 || String(this.user_agent).indexOf('android') > -1){
			ReactNativeWebView.postMessage("MOB001");
		}
		
		axios.get('/back/Common/InnerIP')
		.then(response => {
			this.user_ip_b = response.data;
		})
		.catch(error => {
			console.log(error)
		});
	},
	watch: {

	},
	methods: {
		LoginConfirm() {
			if(this.username == '' || this.password == ''){
				alert('ID와 비밀번호를 입력해주시기 바랍니다.');
				return;
			}
		
			axios.post('/back/Login/Login', null, { params: {
				clm_user_id: this.username,
				clm_user_pw: this.password
			}})
			.then(response => {
				if (response.status == '200' && response.data.clm_user_id != '') {
					this.$session.start();
					this.$session.set('user_id', response.data.clm_user_id);
					this.$session.set('user_name', response.data.clm_user_name);
					this.$session.set('user_machine_yn', response.data.clm_machine_yn);
					this.$session.set('user_machine_type', response.data.clm_machine_type);
					// alert('정상적으로 로그인 되었습니다.');
					location.reload();
				}else{
					alert('ID와 패스워드를 확인해주시기 바랍니다.');
					this.$session.destroy();
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		LogOut() {
			if(confirm("로그아웃 하시겠습니까?")){
				this.$session.destroy();
				//this.loginModal = true;
				location.reload();
			}
		},
		fnc_bell() {
			/*
			axios.get('/back/Common/MessageList',{
				params: {
					user_id: this.user_id
				}
			})
			.then(response => {
				this.bell_items = response.data;
				document.activeElement.blur();
				if(String(response.data.length) == '0'){
					this.bell_gubun = 'mdi-bell'
				}else{
					this.bell_gubun = 'mdi-bell-ring';
					var msg_audio = document.getElementById("msg_audio");
					msg_audio.play();
				}
				console.log(this.bell_items);
			});
			*/
		},
		fnc_Message_Confirm(){
			document.activeElement.blur();
			if(typeof this.bell_items[Number(this.bell_index)] == 'undefined'){
				if(this.MessageModal == true){
					this.MessageModal = false;
				}
				this.bell_index = '0'
				this.bell_gubun = 'mdi-bell'
			}else{
				if(this.MessageModal == false){
					this.MessageModal = true;
				}
				this.msg_id = this.bell_items[Number(this.bell_index)].clm_message_id
				this.msg_sender_name = this.bell_items[Number(this.bell_index)].clm_sender_user_name
				this.msg_sender_datetime = this.bell_items[Number(this.bell_index)].clm_sender_datetime
				this.msg_title = this.bell_items[Number(this.bell_index)].clm_message_title
				this.msg_text = this.bell_items[Number(this.bell_index)].clm_message_text

				axios.post('/back/Common/MSGConfirm', null, { params: {
					msg_id:this.msg_id
				}})
				.then(response => {
					
				});
				this.bell_index = String(Number(this.bell_index)+1)
			}
			
		},
		fnc_bell_click(){
			if(this.bell_items.length > 0){
				this.fnc_Message_Confirm();
			}else{
				alert('메시지가 없습니다.');
			}
		},
		OpenPackage(data){
			console.log(data)
			this.title_joborder_id=data.clm_joborder_id;
			this.clm_high_count = '';
			this.clm_middle_high_count ='';
			this.clm_middle_count ='';
			this.clm_low_count ='';
			this.clm_bones_count='';
			this.clm_bones_high_count='';
			this.clm_high_bun_count='';
			//disabled 구분
			this.clm_high_yn = 'N';
			this.clm_middle_high_yn = 'N';
			this.clm_middle_yn = 'N';
			this.clm_low_yn = 'N';
			this.clm_bones_yn = 'N';
			this.clm_bones_high_yn = 'N';
			this.clm_high_bun_yn = 'N';
			this.PackageModal = true;
		},
		SavePackageCount(){
			axios.post('/back/MOB001/RegPackageCount', null, { params: {
				clm_joborder_id: this.title_joborder_id,
				clm_high_count: this.clm_high_count,
				clm_middle_high_count: this.clm_middle_high_count,
				clm_middle_count: this.clm_middle_count,
				clm_low_count: this.clm_low_count,
				clm_bones_count: this.clm_bones_count,
				clm_bones_high_count: this.clm_bones_high_count,
				clm_high_bun_count:this.clm_high_bun_count,
				clm_user: this.$session.get('user_id'),
			}})
			.then(response => {
				if(response.status == '200') {
					this.loading_yn = 'N';
					this.PackageModal = false;
					this.getList();
				}else{
					this.loading_yn='N';
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		getList() {
			this.selected_items = [];
			const vm = this
			this.loading = true		
			this.save_yn='Y';
			return new Promise((resolve, reject) => {
				const { sortBy, sortDesc, page, itemsPerPage } = this.options
				let sort = []
				let TsortBy = ''
				let TsortDesc = ''
				let items = axios.get('/back/MOB001/List',{
						params: {
							search_date: this.search_date,
							search_gubun: this.search_gubun,
							user_id: this.$session.get('user_id'),
							page: page,
							PerPage: itemsPerPage
						}
					}).then(
					response => {
						console.log(response.data)
						items = response.data
						setTimeout(() => {
							vm.loading = false
							this.document = items	
							this.save_yn='N';
						}, 1000)
					},
				)
			})
		},
		onClickRow(event, data) {

		},
		setDate(){ 
			let year = new Date().getFullYear();
			let month =new Date().getMonth() + 1 < 10? "0" + (new Date().getMonth() + 1): new Date().getMonth() + 1;
			let date =new Date().getDate() < 10? "0" + new Date().getDate(): new Date().getDate();
			let hh =new Date().getHours() < 10? "0" + new Date().getHours(): new Date().getHours();
			let mm =new Date().getMinutes() < 10? "0" + new Date().getMinutes(): new Date().getMinutes();
			let ss =new Date().getSeconds() < 10? "0" + new Date().getSeconds(): new Date().getSeconds();
			return { 
				'year' : year , 
				'month' : month , 
				'date' : date , 
				'hh' : hh , 
				'mm' : mm , 
				'ss' : ss 
			} 
		},
		fnc_router_go(location){
			alert('현재 사용이 불가능합니다.');
			return;
			this.$router.replace(location);
		},
		msgCall(){
			if(this.selected_items.length < 1) {
				alert('선택된 작업지시서가 없습니다.');
				return;
			}else if(this.selected_items.length > 1){	
				alert('선택된 작업지시서가 잘못 되었습니다.');
				return;
			}
			if(this.loading_yn == 'Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			var selected_joborder_id = this.selected_items[0].clm_joborder_id;
			this.m_msg_title = this.user_name + '에서 견적일시 ' + selected_joborder_id + ' 호출이 발생하였습니다.';
			if(confirm("작업지시서 " + selected_joborder_id + "을 사무실에 호출 하시겠습니까?")){
				axios.post('/back/Common/MSGSave', null, { params: {
					clm_msg_title: this.m_msg_title,
					clm_user:this.$session.get('user_id'),
				}})
				.then(response => {
					if (response.status == '200') {
						this.loading_yn='N';
						this.MessageModal = false;
						alert('정상적으로 호출되었습니다.');
						this.getList();						
					}else{
						this.loading_yn='N';
					}
				})
				.catch(error => {
					console.log(error)
				})
			}else{
				this.loading_yn='N';
			}
		}
	}
}
</script>